/* Grid & General styling */
@grid-gap: 24px;

#get-width(@w){

    flex-basis: (100% / 24 * @w);
    flex-grow: 0;
    flex-shrink: 0;
}

.cols {
    display: flex;
    flex-wrap: wrap;
    margin-left: -(@grid-gap / 2);
    margin-right: -(@grid-gap / 2);
}

.c24-1,
.c24-2,
.c24-3,
.c24-4,
.c24-5,
.c24-6,
.c24-7,
.c24-8,
.c24-9,
.c24-10,
.c24-11,
.c24-12,
.c24-13,
.c24-14,
.c24-15,
.c24-16,
.c24-17,
.c24-18,
.c24-19,
.c24-20,
.c24-21,
.c24-22,
.c24-23,
.c24-24 {
    flex: 1;
    box-sizing: border-box;
    padding-left: (@grid-gap / 2);
    padding-right: (@grid-gap / 2);
    margin-bottom: @grid-gap;
}

